import React, { Fragment } from 'react'
import { hot } from 'react-hot-loader/root'
//import { connect, Provider } from 'react-redux'
import universal from 'react-universal-component'
import styles from '../css/App'
import { pages, nextIndex, indexFromPath } from '../utils'
import Home from './Pages/Home'
import Loading from './Loading'
import NotFound from './NotFound'
//import configureStore from '../store/configure'

//const store = configureStore();
const determineHowToLoad = ({ page }) => typeof page !== 'string' ? () => page() : import(`./${page}`)

const UniversalComponent = universal(determineHowToLoad, {
  onError: error => {
    throw error
  },
  minDelay: 1200,
  loading: Loading,
  error: NotFound
})

class App extends React.Component {
  render() {
    const { index, done, loading } = this.state
    return (
      <Fragment>
        <UniversalComponent
          page='Pages/Home'
          onBefore={this.beforeChange}
          onAfter={this.afterChange}
          onError={this.handleError}
        />
      </Fragment>
    )
  }

  constructor(props) {
    super(props)
    this.state = {
      loading: false,
      done: false,
      error: false
    }
  }


  changePage = () => {
    const { loading, index } = this.state
    const { history } = this.props
    if (loading) return

    const idx = nextIndex(index)
    const page = pages[idx]

    history.push(`/${page}`)
  }

  beforeChange = ({ isSync }) => {
    if (!isSync) {
      this.setState({ loading: true, error: false })
    }
  }

  afterChange = ({ isSync, isServer, isMount }) => {
    if (!isSync) {
      this.setState({ loading: false, error: false })
    }
    else if (!isServer && !isMount) {
      this.setState({ done: true, error: false })
    }
  }

  handleError = error => {
    this.setState({ error: true, loading: false })
  }

  buttonText() {
    const { loading, error } = this.state
    if (error) return 'ERROR'
    return loading ? 'LOADING...' : 'CHANGE PAGE'
  }
}
// const Apps = connect((store) => {return { page:store.routes }} )(App)
// const Provided = () => (<Provider store={store}><Apps /></Provider>)
export default hot(App)
